    public static GetIndexBySort(d: number[]) {
        let len = d.length;
        let indices = new Array(len);
        for (var i = 0; i < len; ++i) { indices[i] = i; }
        indices.sort((a, b) => { return d[a] < d[b] ? -1 : d[a] > d[b] ? 1 : 0; });
        return indices;
    }

    public static GetIndexBySortDescending(d: number[]) {
        let len = d.length;
        let indices = new Array(len);
        for (var i = 0; i < len; ++i) { indices[i] = i; }
        indices.sort((a, b) => { return d[a] > d[b] ? -1 : d[a] < d[b] ? 1 : 0; });
        return indices;
    }

    public static SortHeapAscending(d: number[]) {
        let len = d.length;
        for (let i = 0; i < len; ++i) {
            let c = i;
            do {
                let root = Math.round((c - 1) / 2);
                if (d[root] < d[c]) {
                    let temp = d[root];
                    d[root] = d[c];
                    d[c] = temp;
                }
                c = root;

            } while (c !== 0);
        }
        for (let i = len - 1; i >= 0; i--) {
            let temp = d[0];
            d[0] = d[i];
            d[i] = temp;
            let root = 0;
            let c = 1;

            do {
                c = 2 * root + 1;
                if (c < i - 1 && d[c] < d[c + 1]) {
                    c++;
                }
                if (c < i && d[root] < d[c]) {
                    temp = d[root];
                    d[root] = d[c];
                    d[c] = temp;
                }
                root = c;
            } while (c < i);
        }
        return d;
    }
