import { NGeoJSON } from './NGeoJSON';
import { NGeoJSONPolygon } from './NGeoJSONPolygon';

export class DataUtilities {
    // tslint:disable-next-line:no-any
    datum: any; data: any;

    // tslint:disable-next-line:no-any
    static GetShapeFromGeoJsonPTFromFeatureService(x: number[], y: number[], z: number[]) {
        let vers: number[] = [];
        for (let i = 0; i < x.length; ++i) { vers.push(x[i], y[i], z[i] * 0.1); }
        return vers;
    }
    constructor() {
        this.datum = [];
    }
    OpenJson(path: string) {
        // console.log(d);
    }
    // tslint:disable-next-line:no-any
    SetData(d: any) {
        this.datum.push(d);
        this.data = d;
        // tslint:disable-next-line:no-any
        (window as any).d = d;
        console.log(this.datum);
    }
    // ............................................... processing

    // tslint:disable-next-line:no-any
    GetShapeFromGeoJsonLN(da: any) {
        if (da === undefined || da.features === undefined) { console.debug('no validated geojson data processed'); return; }
        // let Point, MultiPoint, LineString, MultiLineString, Polygon, MultiPolygon, GeometryCollection = [];
        let vers: number[] = [];
        for (let d of da.features) {
            for (let v of d.geometry.coordinates) {
                // console.log(v);
                // vers = vers.concat(v);

                vers.push(v[0], v[1]);
                // }
            }
        }
        console.log(vers);
        // tslint:disable-next-line:no-any
        (window as any).d = vers;
    }
    // tslint:disable-next-line:no-any
    GetShapeFromGeoJsonPT(da: any) {
        if (da === undefined || da.features === undefined) { console.debug('no validated geojson data processed'); return; }
        // let Point, MultiPoint, LineString, MultiLineString, Polygon, MultiPolygon, GeometryCollection = [];
        let vers: number[] = [];

        console.log(da);

        for (let d of da.features) {
            // for (let v of d.geometry.coordinates) {
            //     // console.log(v);
            //     // vers = vers.concat(v);
            // console.log(d.properties.mag);
            vers.push(d.geometry.coordinates[0], d.geometry.coordinates[1], d.properties.mag * d.properties.mag * 2);
            //     // }
            // }
        }
        // tslint:disable-next-line:no-any
        (window as any).d = vers;

        // tslint:disable-next-line:no-any
        (window as any).dMat = new NGeoJSON(da);
    }
    // tslint:disable-next-line:no-any
    GetShapeFromGeoJsonPTFromFeatureService(da: any) {
        let vers: number[] = [];
        for (let i = 0; i < da.dA.length; ++i) { vers.push(da.dA[i], da.dB[i], da.dF[i] * 0.1); }
        // tslint:disable-next-line:no-any
        (window as any).featureD = vers;
        // tslint:disable-next-line:no-any
        (window as any).featureC = da.dE;
    }
    // tslint:disable-next-line:no-any
    GetShapeFromGeoJsonPTFromFeatureServiceForDynamic(da: any) {
        let mat: number[][] = [];
        for (let j = 0; j < da.dA.length; ++j) {
            let vers: number[] = [];
            for (let i = 0; i < da.dA.length; ++i) {
                if (Math.random() < 0.2) {
                    vers.push(da.dA[i], da.dB[i], da.dF[i] * 0.1);
                } else {
                    vers.push(0, 0, 0 * 0.1);
                }
            }
            mat.push(vers);
        }
        // tslint:disable-next-line:no-any
        (window as any).featureDMat = mat;
        // tslint:disable-next-line:no-any
        (window as any).featureCMat = da.dE;
    }
    // tslint:disable-next-line:no-any
    GetShapeFromGeoJsonCity(da: any) {
        // tslint:disable-next-line:no-any
        (window as any).dMat = new NGeoJSONPolygon(da);
    }
}
