    static vTerrain = `#version 300 es
    precision mediump float;

    in vec4 a_position;
    in vec3 a_normal;

    uniform mat4 u_matrix;
    // uniform vec2 u_mouse;
    // uniform vec2 u_cVec;
    
    out vec3 vLighting;
    out vec4 v_color;

    void main() { 
        gl_Position = u_matrix * a_position;

        highp vec3 ambientLight = vec3(0.3, 0.3, 0.3);
        highp vec3 directionalLightColor = vec3(0.5, 0.5, 0.75);
       
        highp vec3 directionalVector = vec3(0.85, 1.8, 0.75);

        // highp vec4 transformedNormal = u_matrix * vec4(a_normal, 1.0);
        // highp float directional = max(dot(transformedNormal.xyz, directionalVector), 0.0);
        // vLighting = ambientLight + (directionalLightColor * directional);

        highp float directional = max(dot(a_normal.xyz, directionalVector), 0.0);
        vLighting = ambientLight + (directionalLightColor * directional);

        v_color = vec4(a_normal,1);
    }
    `;

