export enum GL_DRAWING_TYPE {
    POINTS, LINES,
    LINE_LOOP, LINE_STRIP,
    TRIANGLES, TRIANGLE_STRIP, TRIANGLE_FAN
}

export class GLCommon {
    static ResizeCanvas(canvas: HTMLCanvasElement, multiplier: number = 1) {
        let width = canvas.clientWidth * multiplier | 0;
        let height = canvas.clientHeight * multiplier | 0;
        if (canvas.width !== width || canvas.height !== height) {
            canvas.width = width;
            canvas.height = height;
            return true;
        } else {
            return false;
        }
    }

}
