
export class Vec3 extends Float32Array {

    isModified: boolean = true;

    static FromValues(x: number = 0, y: number = 0, z: number = 0) {
        return new Vec3(x, y, z);
    }
    static scalar(v: Vec3, s: number, out: Vec3) {
        out[0] = v[0] * s;
        out[1] = v[1] * s;
        out[2] = v[2] * s;
        return out;
    }
    static Normalize(v: Vec3) {
        let mag = Math.sqrt(v[0] * v[0] + v[1] * v[1] + v[2] * v[2]);

        v[0] = v[0] / mag;
        v[1] = v[1] / mag;
        v[2] = v[2] / mag;
        v.isModified = true;
        return v;
    }
    static ScalarRev(v: Vec3, s: number, out: Vec3) {
        out[0] = s * v[0];
        out[1] = s * v[1];
        out[2] = s * v[2];
        return out;
    }
    static Dot(a: Vec3, b: Vec3) { return a[0] * b[0] + a[1] * b[1] + a[2] * b[2]; }

    static Cross(a: Vec3, b: Vec3, out: Vec3) {
        let ax = a[0], ay = a[1], az = a[2],
            bx = b[0], by = b[1], bz = b[2];
        out[0] = ay * bz - az * by;
        out[1] = az * bx - ax * bz;
        out[2] = ax * by - ay * bx;
        return out;
    }
    static Lerp(out: Vec3, a: Vec3, b: Vec3, t: number) {
        let ax = a[0];
        let ay = a[1];
        let az = a[2];
        out[0] = ax + t * (b[0] - ax);
        out[1] = ay + t * (b[1] - ay);
        out[2] = az + t * (b[2] - az);
        return out;
    }
    // From a point in space, closest spot to a 3D line
    static ClosestPointToLine3D(a: Vec3, b: Vec3, p: Vec3, out: Vec3) {
        if (out === undefined) { out = new Vec3(); }
        var dx = b.x - a.x,
            dy = b.y - a.y,
            dz = a.z - a.z,
            t = ((p.x - a.x) * dx + (p.y - a.y) * dy + (p.z - a.z) * dz) / (dx * dx + dy * dy + dz * dz),
            x = a.x + (dx * t),
            y = a.y + (dy * t),
            z = a.z + (dz * t);
        return out.Set(x, y, z);
    }
    constructor(x: number = 0, y: number = 0, z: number = 0) {
        super(3);
        this.x = x;
        this.y = y;
        this.z = z;
        /* */
    }

    set x(v: number) { this[0] = v; } get x() { return this[0]; }
    set y(v: number) { this[1] = v; } get y() { return this[1]; }
    set z(v: number) { this[2] = v; } get z() { return this[2]; }

    Set(x: number = 0, y: number = 0, z: number = 0) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
    Length(v: Vec3) {
        // Only get the magnitude of this vector
        if (v === undefined) {
            return Math.sqrt(this[0] * this[0] + this[1] * this[1] + this[2] * this[2]);
        } else {
            // Get magnitude based on another vector
            let x = this[0] - v[0],
                y = this[1] - v[1],
                z = this[2] - v[2];
            return Math.sqrt(x * x + y * y + z * z);
        }
    }
    LengthSqr(v: Vec3) {
        if (v === undefined) {
            return this[0] * this[0] + this[1] * this[1] + this[2] * this[2];
        } else {
            // Get squared magnitude based on another vector
            let x = this[0] - v[0],
                y = this[1] - v[1],
                z = this[2] - v[2];
            return x * x + y * y + z * z;
        }
    }
    Normalize() {
        return Vec3.Normalize(this);
    }
    Scale(v: number, out: Vec3) {
        out = out || this;
        out[0] = this[0] * v;
        out[1] = this[1] * v;
        out[2] = this[2] * v;
        if (out === this) { this.isModified = true; }
        return this;
    }
    Mul(v: Vec3, out: Vec3) {
        out = out || this;
        out[0] = this[0] * v[0];
        out[1] = this[1] * v[1];
        out[2] = this[2] * v[2];
        if (out === this) { this.isModified = true; }
        return this;
    }
    Add(v: Vec3, out: Vec3) {
        out = out || this;
        out[0] = this[0] + v[0];
        out[1] = this[1] + v[1];
        out[2] = this[2] + v[2];
        if (out === this) { this.isModified = true; }
        return this;
    }
    Sub(v: Vec3, out: Vec3) {
        out = out || this;
        out[0] = this[0] - v[0];
        out[1] = this[1] - v[1];
        out[2] = this[2] - v[2];
        if (out === this) { this.isModified = true; }
        return this;
    }
    Div(v: Vec3, out: Vec3) {
        out = out || this;
        out[0] = (v[0] !== 0) ? this[0] / v[0] : 0;
        out[1] = (v[1] !== 0) ? this[1] / v[1] : 0;
        out[2] = (v[2] !== 0) ? this[2] / v[2] : 0;
        if (out === this) { this.isModified = true; }
        return this;
    }
    DivInvScale(v: number, out: Vec3) {
        out = out || this;
        out[0] = (this[0] !== 0) ? v / this[0] : 0;
        out[1] = (this[1] !== 0) ? v / this[1] : 0;
        out[2] = (this[2] !== 0) ? v / this[2] : 0;
        if (out === this) { this.isModified = true; }
        return this;
    }
    Clone() { return new Vec3(this.x, this.y, this.z); }
    Copy(v: Vec3) {
        this[0] = v[0]; this[1] = v[1]; this[2] = v[2];
        this.isModified = true;
        return this;
    }
    nearZero(out: Vec3) {
        out = out || this;

        if (Math.abs(out[0]) <= 1e-6) { out[0] = 0; }
        if (Math.abs(out[1]) <= 1e-6) { out[1] = 0; }
        if (Math.abs(out[2]) <= 1e-6) { out[2] = 0; }

        return this;
    }
    TransformMat3(m: Float32Array, out: Vec3, ) {
        let x = this[0], y = this[1], z = this[2];
        out = out || this;
        out[0] = x * m[0] + y * m[3] + z * m[6];
        out[1] = x * m[1] + y * m[4] + z * m[7];
        out[2] = x * m[2] + y * m[5] + z * m[8];
        return out;
    }
    TransformMat4(m: Float32Array, out: Vec3, ) {
        let x = this[0], y = this[1], z = this[2], w = m[3] * x + m[7] * y + m[11] * z + m[15];
        w = w || 1.0;

        out = out || this;
        out[0] = (m[0] * x + m[4] * y + m[8] * z + m[12]) / w;
        out[1] = (m[1] * x + m[5] * y + m[9] * z + m[13]) / w;
        out[2] = (m[2] * x + m[6] * y + m[10] * z + m[14]) / w;
        return out;
    }
}