
export enum GLBLENDING_FUNCTION {
    ZERO = 0,
    ONE = 1,
    SRC_COLOR = 768,
    ONE_MINUS_SRC_COLOR = 769,
    DST_COLOR = 774,
    ONE_MINUS_DST_COLOR = 775,
    SRC_ALPHA = 770,
    ONE_MINUS_SRC_ALPHA = 771,
    DST_ALPHA = 772,
    ONE_MINUS_DST_ALPHA = 773,
    CONSTANT_COLOR = 32769,
    ONE_MINUS_CONSTANT_COLOR = 32770,
    CONSTANT_ALPHA = 32771,
    ONE_MINUS_CONSTANT_ALPHA = 32772,
    SRC_ALPHA_SATURATE = 776
}

export class GLBlending {
    static numSource: number = 0;
    static numDestination: number = 0;
    static glBlendingArray = [
        'ZERO', 'ONE',
        'SRC_COLOR', 'ONE_MINUS_SRC_COLOR',
        'DST_COLOR', 'ONE_MINUS_DST_COLOR',
        'SRC_ALPHA', 'ONE_MINUS_SRC_ALPHA',
        'DST_ALPHA', 'ONE_MINUS_DST_ALPHA',
        'CONSTANT_COLOR', 'ONE_MINUS_CONSTANT_COLOR',
        'CONSTANT_ALPHA', 'ONE_MINUS_CONSTANT_ALPHA',
        'SRC_ALPHA_SATURATE'
    ];

    private indexSource: number = 0;
    private indexDestination: number = 1;
    private glSource: number;
    private glDestination: number;

    static GLBlendingType(gl: WebGLRenderingContext, sMode: GLBLENDING_FUNCTION, dMode: GLBLENDING_FUNCTION) {
        gl.blendFunc(sMode, dMode);
    }
    static GetSourceBlendingInOrder() {
        /* */
    }
    static GetDestinationBlendingInOrder() {
        /**/
    }

    constructor() {
        this.indexSource = 6;
        this.indexDestination = 1;
        this.glSource = GLBLENDING_FUNCTION[GLBlending.glBlendingArray[this.indexSource]];
        this.glDestination = GLBLENDING_FUNCTION[GLBlending.glBlendingArray[this.indexDestination]];
        // this.GetLog();
    }
    ShiftSource() {
        if (++this.indexSource === GLBlending.glBlendingArray.length) { this.indexSource = 0; }
        this.glSource = GLBLENDING_FUNCTION[GLBlending.glBlendingArray[this.indexSource]];
        this.GetLog();
    }
    ShiftDestination() {
        if (++this.indexDestination === GLBlending.glBlendingArray.length) { this.indexDestination = 0; }
        this.glDestination = GLBLENDING_FUNCTION[GLBlending.glBlendingArray[this.indexDestination]];
        this.GetLog();
    }
    GetLog() {
        console.log(GLBlending.glBlendingArray[this.indexSource], '|', GLBlending.glBlendingArray[this.indexDestination]);
    }
    UpdateBlending(gl: WebGLRenderingContext) {
        gl.blendFunc(this.glSource, this.glDestination);
    }

}